/**
 * @fileOverview 
 */
var idPrefix = 'WU_FILE_',
    idSuffix = 0,
    rExt = /\.([^.]+)$/,
    statusMap = {};

function gid () {
    return idPrefix + idSuffix++;
}
/**
 * 
 * @class File
 * @constructor 
 * @grammar new File( source ) => File
 * @param {Lib.File} source [lib.File](#Lib.File), sourceRuntime
 */
export function WUFile (source,opt) {
    this.eventEmitter = opt.eventEmitter;
    let arrKeys = Object.keys(source);
    for(let i in arrKeys) {
        this[arrKeys[i]] = source[arrKeys[i]];
    }
    /**
     * IDID
     * @property id
     * @type {string}
     */
    this.id = gid();
    /**
     * 
     * @property name
     * @type {string}
     */
    this.name = source.name || opt.setName(this.id) || 'Untitled';

    let ext = rExt.exec(source.name) ? RegExp.$1.toLowerCase() : '';
    /**
     * test.pngpng
     * @property ext
     * @type {string}
     */
    this.ext = ext;

    if (!this.ext && source.type) {
        ext = /\/(jpg|jpeg|png|gif|bmp)$/i.exec(source.type) ?
            RegExp.$1.toLowerCase() : '';
        if(!!ext) {
            this.name += '.' + ext;
        }
    }


    this.path = source.path || 'Untitled';

    this.isFile = true;
    /**
     * 
     * @property size
     * @type {uint}
     * @default 0
     */
    this.size = source.size || 0;

    /**
     * MIMETYPE[http://t.cn/z8ZnFny](http://t.cn/z8ZnFny)
     * @property type
     * @type {string}
     * @default 'application/octet-stream'
     */
    this.type = source.type || 'application/octet-stream';

    /**
     * 
     * @property lastModifiedDate
     * @type {int}
     * @default 
     */
    this.lastModifiedDate = source.lastModifiedDate || (new Date() * 1);


    /**
     * status
     * @property statusText
     * @type {string}
     */
    this.statusText = 'inited';

    // 
    statusMap[this.id] = WUFile.Status.INITED;

    this.source = source;
    this.loaded = 0;
}

WUFile.prototype = {
    constructor: WUFile,

    /**
     * `change`
     * @method setStatus
     * @grammar setStatus( status[, statusText] );
     * @param {File.Status|String} status [](#WebUploader:File:File.Status)
     * @param {String} [statusText=''] errorhttp, abort,server
     */
    setStatus: function (status, text) {
        var prevStatus = statusMap[this.id];
        typeof text !== 'undefined' && (this.statusText = text);
        if (status !== prevStatus) {
            statusMap[this.id] = status;
            /**
             * 
             * @event statuschange
             */
            this.eventEmitter.trigger('statuschange', status, prevStatus);
        }
    },

    /**
     * 
     * @return {File.Status}
     * @example
     
     {
         // 
        INITED:     0,
        // 
        QUEUED:     1,
        // 
        PROGRESS:     2,
        // 
        ERROR:         3,
        // 
        COMPLETE:     4,
        // 
        CANCELLED:     5
    }
     */
    getStatus: function () {
        return statusMap[this.id];
    },

    /**
     * 
     * @return {*}
     */
    getSource: function () {
        return this.source;
    },

    destroy: function () {
        delete statusMap[this.id];
    }
};

/**
 * 
 * * `inited` 
 * * `queued` , 
 * * `progress` 
 * * `complete` 
 * * `error` 
 * * `interrupt` 
 * * `invalid` 
 * * `cancelled` 
 * @property {Object} Status
 * @namespace File
 * @class File
 * @static
 */
WUFile.Status = {
    INITED: 'inited',    // 
    QUEUED: 'queued',    // , 
    PROGRESS: 'progress',    // 
    ERROR: 'error',    // 
    COMPLETE: 'complete',    // 
    CANCELLED: 'cancelled',    // 
    INTERRUPT: 'interrupt',    // 
    INVALID: 'invalid'    // 
};



